body {
  background: #D3D3D3; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px; }

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: #fff;
  text-align: center; }

.App-title {
  font-size: 1.5em; }

.App-intro {
  font-size: large; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

/* searchForm  */
.searchForm {
  background: #D3D3D3; }
  .searchForm__searchInput {
    width: 100%;
    outline: none;
    background: #C8C8C8; }
    .searchForm__searchInput::-webkit-input-placeholder {
      color: #000;
      font-weight: 400; }
    .searchForm__searchInput:-moz-placeholder {
      color: #000;
      font-weight: 400; }
    .searchForm__searchInput::-moz-placeholder {
      color: #000;
      font-weight: 400; }
    .searchForm__searchInput:-ms-input-placeholder {
      color: #000;
      font-weight: 400; }
  .searchForm__searchBtn, .searchForm__saveBtn {
    background: #C8C8C8;
    width: 48%; }
  .searchForm__searchBtn {
    float: left; }
  .searchForm__saveBtn {
    float: right; }
  .searchForm__imageGrid {
    background: #B8B8B8;
    min-height: 300px; }
  .searchForm__savedQueries {
    background: #B8B8B8;
    min-height: 300px; }

